import { Button, Center, Container, PasswordInput, Stack, TextInput } from "@mantine/core";
export default function Login() {

  return (
    <Center h={200}>
      <Container size="sm">
        <Stack w={400}>
          <TextInput
            label="Почта"
            placeholder="Иванов@mail.ru"
            required
          />
          <PasswordInput
            label="Пароль"
            placeholder="Пароль"
            required
          />
          <Button>Войти</Button>
        </Stack>
      </Container>
    </Center>
  )
}